.products 
{
    margin-top : 50px;
    width : 100vw;
    min-height : 50vh;
    

    @include flexCenterCol;
    justify-content: space-around;

    &_heading 
    {
        @include sectionHeading;
        
        span 
        {
            @include sectionHeadingSpan;
        }

        &::after 
        {
            @include headingAfter;
        }
    }

    &_container 
    {
        margin-top : 50px;
        display : grid;
        grid-template-columns: repeat(3,300px);
        gap : 20px;
        row-gap : 40px;
    }
}

.product-card 
{
    display : flex;
    flex-direction: column;
    align-items: center;
    color : $primary_color;
  

    .image-container 
    {
   
        height : 60%;
        width : 100%;
        overflow : hidden;
        
        img 
        {
            height : 100%;
            width : 100%;
            object-fit: cover;
            border-radius: 6px;

            &:hover 
            {
                transform : scale(1.05,1.05);
            }
            transition : 0.3s;
        }
        
    }

    &_name 
    {
        font-family: $primary_font;
        font-size: 17px;
        margin-top: 15px;
        font-weight: map-get($font_weights,'regular');
        letter-spacing: -0.5px;
    }
    &_price 
    {
        font-size: 15px;
        margin-bottom : 10px;
        margin-top : 5px;
        font-family: $primary_font;
        font-weight: map-get($font_weights,'light');
        letter-spacing: 1px;
    }

    &_btn 
    {
        height : 50px;
        width : 200px;
        outline : none;
        border : none;
        background : #f7b801;
        font-family: $primary_font;
        color : black;
        margin-bottom: 10px;
        margin-top: 10px;
        cursor: pointer;
        font-weight: map-get($font_weights,'light');
        font-size: 14px;
        border-radius : 3px;
        transition : 0.3s;


        i 
        {
            margin-right : 7px;
        }
        
        &:hover 
        {
            background : #f8961e;
        }
    }
}