.footer 
{
    display : flex;
    width : 100vw;
    height : 10vh;
    background : #212529;
    flex-direction: row;
    justify-content: space-around;

    .terms 
    {
        display : flex;
        flex-direction: row;
        width : 320px;
        align-items: center;
        justify-content: space-around;

        a 
        {
            font-size: 10px;
            color : #ced4da;
            cursor: pointer;

            &:hover 
            {
                text-decoration: underline;
            }
        }
    }

    .newsletter 
    {
        width : 280px;
        align-items: center;
        display: flex;
        flex-direction: row;
        justify-content: space-around;

        input 
        {
            height : 28px;
            border : none;
            outline : none;
            padding : 5px;
            border-radius: 3px;
            background : #e9ecef;
        }

        button 
        {
            outline : none;
            border : none;
            background : #80ed99;
            color : $primary_color;
            padding : 10px;
            font-family: $primary_font;
            border-radius: 3px;
            cursor : pointer;
            @include transitionEase;

            &:hover 
            {
                background : #57cc99;
            }
        }
    }

    .social-links
    {
        display : flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width : 120px;
        
        i 
        {
            color : #ced4da;
            cursor : pointer;
            @include transitionEase;

            &:hover 
            {
                transform : translateY(-16%);
            }
            &:nth-child(1):hover 
            {
                color : #f08080;   
            }
            &:nth-child(2):hover 
            {
                color : #4361ee;   
            }
            &:nth-child(3):hover 
            {
                color : #4cc9f0;   
            }
            &:nth-child(4):hover 
            {
                color : #e36414;   
            }
            &:nth-child(5):hover 
            {
                color : #fb5607;   
            }
        }
    }
}