.feedback 
{
    @include flexCenterCol;
    width : 100vw;
    min-height : 50vh;
    margin-top : 50px;
    font-family: $primary_font;

    &_heading 
    {
        @include sectionHeading;
     
        span 
        {
            @include sectionHeadingSpan;
        }

        &::after 
        {
            @include headingAfter;
        }
        
    }

    .feedback-grid 
    {
        margin-top : 50px;
        display : grid;
        grid-template-columns: repeat(3,minmax(auto,370px));
        gap : 20px;
        grid-template-rows: repeat(3,400px);
        
        padding : 20px;
    }
}

.feedback-card 
{
    display : flex;
    flex-direction: column;
    box-shadow : 0 10px 10px rgba(204, 218, 209, 1);
    border-radius: 6px;
    background-color: #f7f7ff;
    color : $primary_color;

    &:nth-child(2)
    {
        transform : translateY(40%) scale(0.9,0.9);

    }
    &:nth-child(3)
    {
        transform : scale(0.7,0.7) translateX(-20%);
    }
    &:nth-child(4)
    {
        transform : translate(30%,40%);
    }
    &:nth-child(5)
    {
        transform : translate(-6%,-10%) scale(0.9,0.9);
        grid-column : 3 / -1;
    }


    &_header 
    {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        width : 100%;
        min-height : 120px;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
    }

    &_pic 
    {
        position: absolute;
        height : 100px;
        width : 100px;
        top : 50px;
        object-fit: cover;
        border-radius: 50%;
        border : 3px solid white;
        box-shadow: 0 3px 5px rgba(0,0,0,0.5);
    }

    &_name 
    {
        margin-top : 40px;
        align-self: center;
        font-size : 20px;
        font-weight: map-get($font_weights,'regular');   
    }

    &_job 
    {
        margin-top : 5px;
        align-self: center;
        font-size: 12px;
        font-weight: map-get($font_weights,'light');
    }

    &_desc 
    {
        text-align: center;
        font-size : 12px;
        font-weight: map-get($font_weights,'light');
        margin : 5px;
        margin-top : 20px;
        line-height: 1.2rem;
    }
}