.contact 
{
    @include flexCenterCol;
    min-height: 70vh;
    width : 100vw;
    margin-top : -200px;
    font-family: $primary_font;
    background-image : linear-gradient(to right,rgba(128, 237, 153, 0.6),rgba(128, 237, 153, 0.5)),url('wallpapercontact.png');
    background-position: center;
    background-size: cover;


    &_div 
    {
        display : grid;
        grid-template-columns : 300px 420px;
        height : 60vh;
        background : white;
        border-radius: 6px;
        color : black;

        &_child 
        {
            color : $primary_color;
            display : flex;
            flex-direction: column;
            padding : 20px;
            justify-content: space-around;

            &_heading 
            {
                font-size : 26px;
                letter-spacing : -1px;
                font-family: 'Montserrat';
                font-weight: map-get($font_weights,'extra-bold');
                text-transform: lowercase;

                &::after 
                {
                    content : '';
                    display : block;
                    width : 20px;
                    height : 2px;
                    background : $primary_color;
                    margin-left : 10px;
                }

                span 
                {
                    font-weight: map-get($font_weights,'regular');
                }
            }

            &_form-ele 
            {
                margin-left : 30px;
                min-height : 10px;
                width : 150px;

                label 
                {
                    letter-spacing: -0.5px;
                    text-transform: lowercase;
                    font-size: 17px;
                    font-family: 'Montserrat';
                    font-weight: 500;
                    transition : 0.3s;
                    &:after 
                    {
                        width : 0px;
                    }
                }

                input 
                {
                    height : 30px;
                    width : 100%;
                    outline : none;
                    border : none;
                    background-color: #e9ecef;
                    margin-top : 7px;
                    border-radius: 3px;
                    padding : 5px;
                    font-weight: map-get($font_weights,'bold');
                    font-family: 'Montserrat';
                    @include transitionEase;

                }
                
                textarea 
                {
                    margin-top : 7px;
                    height : 60px;
                    border : none;
                    border-radius: 3px;
                    background : #e9ecef;
                    outline : none;
                    padding : 5px;
                }

                .btn 
                {
                    width : 250px;
                    height : 46px;
                    outline : none;
                    border : none;
                    background : #80ed99;
                    color : $primary_color;
                    font-family: 'Montserrat';
                    font-weight: map-get($font_weights,'regular');
                    font-size: 20px;
                    border-radius: 3px;
                    letter-spacing: -1.6px;
                    cursor: pointer;
                    text-transform: lowercase;
                    @include transitionEase;

                    &:hover 
                    {
                        background : #57cc99;
                    }

                    i 
                    {
                        margin-left : 5px;
                    }
                }
            }
        }

        &_details 
        {
            display : flex;
            flex-direction: column;
            height : 100%;
            justify-content: space-around;

            &_img 
            {
                height : 250px;
                width : 250px;
                align-self: center;
            }

            &_sub 
            {
                display : flex;
                flex-direction: column;
                height : 120px;
                margin-left : 50px;
                padding : 20px;
                color : $primary_color;
                font-family: $primary_font;
                justify-content: space-around;
                i 
                {
                    margin-right: 3px;
                    position : relative;
                    top : 2px;
                }

                h3
                {
                    font-weight: map-get($font_weights,'regular');
                    font-size: 12px;

                    span 
                    {
                        letter-spacing: -0.5px;
                        font-size: 12px;
                        font-weight: map-get($font_weights,'bold');
                        margin-right : 5px;
                        text-transform: uppercase;

                    }

                }
            }
        }
    }
}

