@include media-1100 
{
    .newprod_container 
    {
        @include gridRowResponsive;
    }
    .plant-card 
    {
        &:nth-child(2)
        {
            grid-column: 3 / -1;
        }
        &:nth-child(3)
        {
            grid-column: 2 / 3;
        }
    }
}

@include media-900
{
    .newprod_container
    {
        @include gridRowResponsive;
        grid-template-columns : repeat(3,250px) !important;
    }
    .plant-card 
    {
        &:nth-child(2)
        {
            grid-column: 3 / -1;
        }
        &:nth-child(3)
        {
            grid-column: 2 / 3;
        }
    }
}

@include media-900
{
    .newprod_container
    {
        @include gridRowResponsive;
        grid-template-columns : repeat(1,300px) !important;
        grid-template-rows: repeat(3,550px) !important;
    }
    .plant-card 
    {
        &:nth-child(1)
        {
            grid-column: 1 / 2;
        }
        &:nth-child(2)
        {
            grid-column: 1 / 2;
        }
        &:nth-child(3)
        {
            grid-column: 1 / 2;
        }
    }
}

