@import url('https://fonts.googleapis.com/css2?family=Caveat+Brush&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;0,800;1,700&display=swap');

*
{
    margin : 0;
    padding : 0;
    box-sizing: border-box;
}   

body 
{
    overflow-x : hidden;
}

$primary_color : #353535;
$seconary_color : #343a40;
$ternary_color : #6c757d;

@mixin flexCenterRow 
{
    display : flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

@mixin flexCenterCol 
{
    display : flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@mixin flexLeftCol 
{
    display : flex;
    flex-direction: column;
}

@mixin gridRowResponsive
{
    display : grid !important;
    grid-template-columns : repeat(3,280px) !important;
    grid-template-rows: repeat(2,570px) !important;
    justify-content: space-around !important;
}


@mixin gridRow
{
    display : grid;
    grid-template-columns : repeat(3,330px);
    grid-template-rows: repeat(1,570px);
    justify-content: space-around;
}

@mixin flexAround
{
    display : flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

@mixin transitionEase 
{
    transition : 0.3s ease-in-out;
}

@mixin afterUnderline 
{
    content : '';
    display : block;
    margin-top : 3px;
    width : 100%;
    height : 2px;
    width : 0%;
    transition : 0.3s;
}

$font_weights : (
    "light" : 200,
    "regular" : 400,
    "semi-bold" : 600,
    "bold" : 700,
    "extra-bold" : 800
);

$primary_font : 'Rubik';
$secondary_font : 'Caveat Brush';

@mixin sectionHeading 
{
    font-weight : map-get($font_weights,'light');
    font-size: 40px;
    border-radius : 20px;
    padding : 20px;
    text-transform: uppercase;
    color : black;
}
@mixin sectionHeadingSpan 
{
    font-weight : map-get($font_weights,'regular');
    font-size : 28px;
    margin-right: 3px;
    letter-spacing: 1px;
    position: relative;
    bottom : 3px;
}
@mixin headingAfter 
{
    
    content : '';
    width : 15%;
    height : 1px;
    background : black;
    display : block;
    margin-left : 10px;  
}

