.about 
{
    width : 100vw;
    min-height : 40vh;
    font-family : $primary_font;
    color : $primary_color;
    margin-top : 50px;
    @include flexCenterCol; 

    &_heading 
    {
        span 
        {
            @include sectionHeadingSpan;
        }
        @include sectionHeading;

        &::after 
        {
            @include headingAfter;
        }
    }

    .about-title 
    {
        @include flexCenterRow;
        width : 330px;
        height : 80px;
        justify-content: center !important; 
    }

    &-para-container 
    {
        padding : 10px;
        width : 750px;
        padding : 15px;
        border-radius : 6px;
        margin-top : 30px;
    }

    &-para 
    {
        font-size : 15px;
        line-height: 1.5rem;
        text-align: center;
        font-weight: map-get($font_weights,'light');
    }
}