@import 'config';

.nav-bar
{

    position: relative;
    width : 100vw;
    height : 15vh;
    box-shadow : 0 1px 5px rgba(0,0,0,0.5);
    @include flexCenterRow;
    font-family : $primary_font;

    .logo-container 
    {
        position : absolute;
        left : 0;
        height : 100%;
        width : 150px;
    
        margin-left : 30px;

        img 
        {
            height : 100%;
            width : 100%;
        }
    }



    .nav-links 
    {
        @include flexCenterRow;
        min-width : 100px;
        padding : 10px;
        list-style-type: none;
        @include transitionEase;

        &__item 
        {
            margin-right : 30px;
            font-weight : map-get($font_weights,"regular");
            cursor : pointer;
            transition : 0.3s;
            font-size : 16px;

            a 
            {
                text-decoration: none;
                color : $primary-color;

                &::after 
                {
                    @include afterUnderline;
                    background : #9ef01a;
                }

                &:hover 
                {
                    color : #9ef01a;
                }
                &:hover::after
                {
                    width : 100%;
                }
            }
            
            

            
        }
    }

    .nav-extra-items 
    {
        position: absolute;
        right : 0;
        margin-right: 30px;
        height : 50px;
        width : 100px;
        @include flexAround;
        @include transitionEase;
        
        i 
        {
            color : $seconary_color;
            font-size: 20px;
            cursor : pointer; 
            margin-right : 20px;
            @include transitionEase;

            &:hover 
            {
                color : $ternary_color;
            }
        }
    }

    .burger-wrapper 
    {
        position: absolute;
        right : 0;
        margin-right: 50px;
        transform : translateX(100vw);
        width : 30px;
        height : 20px;
        transition : 0.3s; 
        cursor: pointer;
        
        .menu-burger 
        {
            position : relative;
            width : 100%;
            height : 4px;
            top : 5px;
            background : $primary-color;
            @include transitionEase;
            
            &::before
            {
                content : '';
                width : 100%;
                height : 100%;
                display : block;
                background : $primary_color;
                position: relative;
                bottom : 6px;
                @include transitionEase;
            }
            &::after
            {
                content : '';
                width : 100%;
                height : 100%;
                display : block;
                background : $primary_color;
                position: relative;
                top : 3px;
                @include transitionEase;
            }
        }
        &:hover > .menu-burger,&:hover > .menu-burger::before,&:hover > .menu-burger::after
        {
            background : $ternary_color;
        }
    }
    
}

.responsive-menu 
{
    position: absolute;
    right : 0;
    top : 109px;
    background-color: white;
    height : 40vh;
    width : 30vw;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 3px;
    @include flexCenterCol;
    transform : translateX(100%);
    @include transitionEase;
    box-shadow : 0 2px 5px rgba(0,0,0,0.5);
    overflow : hidden;

    &.open 
    {
        transform : translateX(0%);
    }

    &__nav-links 
    {
        list-style-type: none;
        min-height : 150px;
        padding : 10px;
        transform : translateX(100vw);
        transition : 0.7s ease-in-out;

        &.open 
        {
            transform : translateX(0%);
        }
    }

    &__nav-item 
    {
        color : black;
        margin-bottom: 15px;
        font-weight: map-get($font_weights,'light');
        font-size: 17px;
        width : 100%;
        cursor: pointer;
        transform : translateX(100vw);
        transition : 0.3s;

        &.open 
        {
            transform : translateX(0%);
        }

        i 
        {
            margin-right : 5px;
        }

        &:hover 
        {
            transform : scale(1.1,1.1);
        }

    }

    &__extra-items 
    {
        @include flexCenterRow;
        justify-content: space-around;
        width : 50%;
        color : black;
        font-size : 20px;
        transition : 0.3s;
        margin-top : 15px;
        transform : translateY(100vh);
        
        &.open 
        {
            transform :  translateY(0%);
        }

        i
        {
            cursor: pointer; 

            &:hover 
            {
                @include transitionEase;
                transform: translateY(-16%);
            }
        }


    }

}

@for $i from 1 through 5
{
    .responsive-menu__nav-item:nth-child(#{$i})
    {
        transition-delay : ($i * 0.1s) + 0.15s;
    }
}


