.facilities 
{
    width : 100vw;
    height : 30vh;
    @include flexCenterRow;
    justify-content: space-around;
    margin-top : 70px;

    &_sub 
    {
        @include flexCenterRow;
        width : 350px;
        height : 120px;
        background : #ffe5d9;
        border-radius: 6px;
        color : $primary_color;
        font-family : $primary_font;

        i 
        {
            margin-left : 60px;
            font-size : 30px;
        }
        &_desc 
        {
            @include flexCenterCol;
            height : 100%;
            width : 200px;
            margin-left : auto;
            align-items: flex-start !important;
            h1 
            {
                font-size : 17px;
                margin-bottom : 10px;
                font-weight : map-get($font_weights,'extra-bold');
            }
            p 
            {
                font-size : 14px;
            }
        }
    }
}