@import 'config.scss';

.slideshow 
{
    @include flexCenterRow;
    height : 80vh;
    width : 100vw;
    overflow : hidden;
    @include transitionEase;
 
    .slideshow-details 
    {
        min-height : 32vh;
        width : 25vw;
        
        &-h1
        {
            font-family : $primary_font;
            width : 100%;
            line-height : 4rem;
            font-size : 60px;
            font-weight : map-get($font-weights,'bold');
            letter-spacing: -1px;
            

            span 
            {
                font-weight : map-get($font-weights,'light');
            }
        }

        &-p 
        {
            font-size : 12px;
            font-family : $primary_font;
            margin : 5px;
            letter-spacing: 1px;
        }

        button 
        {
            margin-top : 20px;
            background : none;
            height : 50px;
            width : 140px;
            outline : none;
            margin-left : 5px;
            cursor: pointer;
            font-family: $primary_font;
            @include transitionEase;

            &.lightbg 
            {
                background : white;
                color : $primary-color !important;
            }
            &.darkbg 
            {
                background : $primary-color;
                color : white !important;
            }

        }

    }

    .img-container 
    {
        height : 85vh;
        width : 40vw;
        align-self : flex-end;
        
        &--image
        {
            margin-top : 100px;
            width : 100%;
            height : 100%;
            object-fit: contain;
            user-select: none;
        }
    }
    
    .arrow 
    {
        @include flexCenterRow;
        height : 40px;
        width : 40px;
        background : white;
        border-radius: 50%;
        cursor : pointer;
        @include transitionEase;
        z-index : 1;

        &.left 
        {
            margin-right : 230px;
        }
        &.right 
        {
            margin-left : 100px;
        }

        i 
        {
            color : $primary-color;
        }

        &:hover 
        {
            background : #f5ebe0;
            box-shadow : 0 2px 2px rgba(0,0,0,0.4);
        }

    }

    .slide-btn-container 
    {
        width : 90px;
        height : 50px;
        position : absolute;
        bottom : 0;
        margin-bottom : 20px;
        @include flexCenterRow;
        justify-content: space-around;

        .slide-btn 
        {
            height : 15px;
            width : 15px;
            border-radius: 50%;    
            border : 2px solid #fff;
            margin-bottom : 20px;
            &.active 
            {
                background : white;
            }
        }
    }

    
}

