.card 
{
    min-height : 70vh;
    width : 100vw;
    font-family: $primary_font;
    @include flexCenterCol;
    align-items: flex-start;
        
    &_heading 
    {
        margin : 30px 0 0 70px;
        color : #38b000;
        font-size : 60px;
        letter-spacing: -2px;
        
        span
        {
            color : #006400;
        }
    }

    &_grid 
    {
        margin-top : 50px;
        display : grid;
        grid-template-columns : 500px 200px 520px;
        grid-template-rows : 300px 250px;
        margin-left : 5vw;
        gap : 20px;

        img 
        {
            width : 100%;
            height : 100%;
        }

        &_1,&_2,&_3,&_4
        {
            display: flex;
            flex-direction: column;
            color : $primary_color; 
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            padding : 20px;
            border-radius: 6px;

            h1 
            {
                font-size : 40px;
                letter-spacing: -0.6px;
                font-weight: map-get($font_weights,'bold');
            }
            p 
            {
                font-size : 15px;
                margin : 5px;
                letter-spacing: -0.5px;
            }
        }

        &_1 
        {
            background-image : url('wallpaper1.png');
            align-items: flex-end;
            grid-column: 1 / 3;
        }
        &_2 
        {
            background-image : url('wallpaper2.png');
            color : white;
        }
        &_3 
        {
            background-image : url('wallpaper3.png');
            grid-column : 1 / 2;
            color : white;
        }
        &_4
        {
            background-image : url('wallpaper4.png');
            color : white;
            grid-column: 2 / -1;
        }

    }

}