.newprod 
{
    width : 100vw;
    min-height : 50vh;
    @include flexCenterCol;
    font-family: $primary_font;
    color : $primary_color;
    justify-content: space-between;
    margin-top: 50px;
    
    &_heading 
    {
        span 
        {
            @include sectionHeadingSpan;
        }
        @include sectionHeading;
        margin-left : 40px;

        &::after 
        {
            @include headingAfter;
        }
    }

    &_container 
    {
        margin-top : 50px;
        width : 100vw;
        @include gridRow;
        background-image : linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.8)),url('wallpaper.png');
        background-position: center;
        background-size: cover;
        padding : 20px;
        

        .plant-card 
        {
            @include flexLeftCol;
            max-height : 96%;
            box-shadow : 0 3px 5px rgba(0,0,0,0.3);
            border-radius: 6px;
            background-color: #fffbff;
            @include transitionEase;

            &_new-tag 
            {
                background-color: #faa916;
                align-self: flex-end;
                padding : 5px;
                font-size : 15px;
                margin : 10px 10px 0 0;
                color : white;
                font-weight: map-get($font_weights,'light');  
                border-radius: 3px;
                font-family : $secondary_font;
            }
            
            &_img 
            {
                margin-top : 25px;
                height : 50%;
                width : 90%;
                margin-left: 10px;
                position: relative; 
            }
            &_details 
            {
                width : 100%;
                height : 200px;
                @include flexCenterCol;
                margin-top : 15px;
                padding-left : 10px;

                &_name 
                {
                    align-self: flex-start;
                    margin : 10px;
                    font-size : 25px;
                    font-weight: map-get($font_weights,'extra-bold');
                    color : #72b01d;
                    letter-spacing: -0.5px;
                }

                &_price 
                {
                    font-size : 17px;
                    align-self: flex-start;
                    margin-left :  10px;
                    position : relative;
                    bottom : 5px;
                    font-weight: map-get($font_weights,'regular');
                }

                &_desc
                {
                    font-size : 12px;
                    margin : 10px;
                }

            }

            &_btn 
            {
                height : 60px;
                width : 150px;
                margin-bottom : 15px;
                align-self: center;
                outline : none;
                background : #f8961e;
                border : none;
                color : black;
                font-weight: map-get($font_weights,'light');
                font-family: $primary_font;
                cursor: pointer;
                font-size : 15px;
                transition : 0.3s;

                &:hover 
                {
                    background : #e36414;
                }
            }


        }
    }

}
